import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import logo from './logo.png';
import MobileMenu from '../../Mobile/Home/MobileMenu/MobileMenu';
const Footer = () => {
    const [position, setPosition] = React.useState({ top: 0, left: 0 })
    React.useEffect(() => {
        window.scroll({
            top: position.top,
            left: position.left,
            behavior: 'smooth'
        })
    })

    const [visibility, setVisibility] = React.useState(false)
    const scrollTop = React.useRef(null)
    React.useEffect(() => {
        window.addEventListener('scroll', (e) => {
            window.scrollY > 200
                ? scrollTop.current.style.display = 'inline-block'
                : scrollTop.current.style.display = 'none'
        })
    })

    const [data, setData] = useState([]);
    useEffect(() => {
        fetch(`http://66.29.142.198:5000/api/frontend/home/footer/setting/view`, {
            method: 'GET',
        })
            .then((res) => res.json())
            .then((data) => setData(data.data))
    }, [])

    const [Newsletter, setNewsletter] = useState([]);
    useEffect(() => {
        fetch(`http://66.29.142.198:5000/api/frontend/home/newsletter/setting/view`, {
            method: 'GET',
        })
            .then((res) => res.json())
            .then((data) => setNewsletter(data.data))
    }, [])

    return (
        <>

            <footer className='footer-area container-custom pt-5 desktop-version'>

                <div className="row">
                    <div className="col-lg-4 col-md-6">
                        <div class="footer__widget">
                            <Link href="/" class="logo">
                                {/* <img src={logo} alt='' /> */}
                                {/* <img src={logo} alt='' /> */}
                                <img src={`http://66.29.142.198:5000/${data?.logo}`} alt="" />
                            </Link>
                            <p> {data?.dis}</p>
                            <ul class="post__share">
                                <li>
                                    <a href="https://t.me/infogffex" target="__blank">
                                        <i class="fa-brands fa-telegram"></i>                              </a>
                                </li>
                                <li>
                                    <a href="https://mail.google.com/mail/u/0/?tab=rm&amp;amp;ogbl#inbox?compose=VpCqJbQVmPcgMbxgmNDfbGBScGbBRhnPxRGdqxFbhvbNfTQLxJwhxJklTZMnqPGPKRWgtxb" target="__blank">
                                        <i class="fa-solid fa-envelope"></i>                             </a>
                                </li>
                                <li>
                                    <a href="https://twitter.com/gffexvip" target="__blank">
                                        <i class="fa-brands fa-twitter"></i>                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6">
                        <div class="footer__widget">
                            <h5 class="title">Quick Links</h5>
                            <ul class="widget__links">
                                <li>
                                    <a href="https://gffexvip.biz"> <i class="fa-solid fa-angles-right"></i> <span >Home</span></a>
                                </li>
                                <li>
                                    <Link href="https://gffexvip.biz/contact"> <i class="fa-solid fa-angles-right"></i><span > Contact </span></Link>
                                </li>
                                <li>
                                    <Link to="/login"> <i class="fa-solid fa-angles-right"></i><span > Sign In </span></Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-lg-2 col-md-6">
                        <div class="footer__widget">
                            <h5 class="title">Policies</h5>
                            <ul class="widget__links">
                                <li>
                                    <Link href="https://gffexvip.biz/policy/about-us/99"><i class="fa-solid fa-angles-right"></i><span > About us </span></Link>
                                </li>
                                <li>
                                    <Link href="https://gffexvip.biz/policy/terms-of-service/94"> <i class="fa-solid fa-angles-right"></i><span > Terms Of Service </span></Link>
                                </li>
                                <li>
                                    <Link href="https://gffexvip.biz/policy/privacy-policy/92"><i class="fa-solid fa-angles-right"></i><span > Privacy Policy </span></Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6">
                        <div class="footer__widget">
                            <h5 class="title">Our Newsletter</h5>
                            <p>{Newsletter?.dis}</p>
                            {/* <p>Subscribe our newsletter to get regullar news and  tips. we promise we won't harass you. Provident inventore dolorum minus</p> */}
                            <form class="subscribe-form" id="subscribe-form">
                                <input type="email" class="form-control subscribe--form--control" required="" name="email" placeholder="Your Email Address" id="email" />
                                <button type="submit" class="cmn--btn">
                                    <i class="fa-regular fa-paper-plane"></i>
                                </button>
                            </form>
                        </div>
                    </div>
                </div>

            </footer>
            <section className='company-info-area desktop-version'>
                <div className="company-info border-custom">
                    <div className="row">
                        <div className="col-4">
                            <div className="d-flex align-items-center">
                                <i class="fa-solid fa-envelope-open-text"></i>
                                <div className="company-info-content">
                                    <h6><Link to={`mailto:${data?.email}`}>{data?.email}</Link></h6>
                                    <span class="info">Email Address</span>
                                </div>
                            </div>
                        </div>
                        <div className="col-4">
                            <div className="d-flex align-items-center">
                                <i class="fa-solid fa-phone-volume"></i>
                                <div className="company-info-content">
                                    <h6><Link to={data?.phone}>{data?.phone}</Link></h6>
                                    <span class="info">Call Us Now</span>
                                </div>
                            </div>
                        </div>
                        <div className="col-4">
                            <div className="d-flex align-items-center">
                                <i class="fa-solid fa-map-location-dot"></i>
                                <div className="company-info-content">
                                    <h6><Link to="#">{data?.address}</Link></h6>
                                    <span class="info">Our Address</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <div className="back-top-btn-area desktop-version" title='back to top' onClick={() => setPosition({ ...position, position: { top: 0, left: 0 } })} ref={scrollTop} style={{}}>
                <i class="fa-solid fa-arrow-up-from-bracket desktop-version"></i>
            </div>
            <div className="support-btn-area desktop-version" title='support'>
                <div className="support-btn">
                    <Link to=''><i class="fa-brands fa-whatsapp"></i></Link>
                    <Link to=''><i class="fa-brands fa-telegram"></i></Link>
                    <Link to=''><i class="fa-brands fa-facebook-messenger"></i></Link>
                    <Link to=''><i class="fa-regular fa-envelope"></i></Link>
                </div>
                <i className="fa-solid fa-headset support-btn-show"></i>
            </div>


            <MobileMenu></MobileMenu>

        </>

    );
};

export default Footer;